@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {

}


.app-main {
  display: flex;
}


.app-left {
  display: flex;
  justify-content: center;
  flex: 0 0 50%;
}

.app-right {
  display: flex;
  justify-content: center;
  flex: 0 0 50%;
  background: #F8FBFD;
  min-height: 80vh;
}

.header {
  padding: 80px 80px;
  border-bottom: 1px solid #ccc;
}

.header-logo {
  width: 2rem;
  height: 2rem;
  border-radius: 2rem;
  border: 0.5rem solid #000;
}

.header-content {
  font-size: 3.5rem;
  font-weight: 300;
  margin: 1rem 0 0;
}

.header-illustration {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
}

.form {
  width: 480px;
  margin-top: 3rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
}

.form-textarea {
  width: 100%;
  font-size: 1.25rem;
  padding: 1rem;
  background: rgba(222, 237, 247, 0.2);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}

.form-input {
  width: 2.5rem;
  font-size: 1.25rem;
  padding: 1rem;
  background: rgba(222, 237, 247, 0.2);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}

.preview {
  width: 480px;
  margin-top: 3rem;
}

